.main-div{
    
    display: flex;
    justify-content: center;
    flex-direction: column;
    
}

.zemin-images img{
    width: 600px;
    height: auto;
}

.zemin-sec{
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 1fr 1fr;
    padding-top: 100px;
    padding-inline: 16%;
    display: flex;
    background: white;
    justify-content: center;
}

.zemin-about h3{
    font-family: 'Montserrat', sans-serif;
    color: #3383c6;
}

.zemin-about p{
    padding-top: 20px;
    padding-bottom: 20px;
    font-family: 'Montserrat', sans-serif;
    color: #707070;
}

.zemin-about h1{
    font-family: 'Montserrat', sans-serif;
    color: #5B5B5B;
}

.zemin-about div{
    display: flex;
    flex-direction: column;
}

.zemin-about button{
    padding: 12px 26px;
    text-align: left;
    color: #5B5B5B;
    width: 450px;
    background-color: #F5F5F5;
    border: none;
    margin-top: 10px;
    font-weight: 500;
}

.zemin-about button:hover{
    cursor: pointer;
    background-color: #479bfc;
    color: white;
    
}