.last-projects-div{
    background-color: rgb(247 247 247);
}

.slide-elemen-div{
    margin: 20px;
    width: 100%;
    height: 300px;
    background-color: rgb(247 247 247);
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.slide-elemen-div h4{
    padding-block: 10px;
}

.slide-elemen-div img{
    width: -webkit-fill-available;
    height: min-content;
}

.last-projects-div{
    padding-block: 50px;
    padding-inline: 15%;
}

.last-proj-h3{
    border-bottom: 3px solid #3383c6;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    color: #464646;
    display: flex;
    padding-bottom: 10px;
    font-weight: 200;
    font-size: 22px;
}

@media(max-width:991px){

    .last-projects-div{
        padding-inline: 5%;
    }



}